import { lightRailApiClient } from "../utils/apiClient";
import { useQuery } from "@sveltestack/svelte-query";

interface Params {
  stationId: number;
}

// https://opendata.mtr.com.hk/doc/LR_Next_Train_DataDictionary_v1.0.pdf
interface LightRailResponse {
  platform_list: {
    platform_id: number;
    route_list: {
      arrival_departure: "A" | "D";
      dest_ch: string;
      dest_en: string;
      route_no: string;
      stop: number;
      time_ch: string;
      time_en: string;
    }[];
  }[];
}

const useLightRailInfo = (params: Params) => {
  return useQuery(
    ["light-rail", params],
    async () => {
      const response = await lightRailApiClient.get<LightRailResponse>(
        "/getSchedule",
        {
          params: {
            station_id: params.stationId,
          },
        }
      );

      return response.data;
    },
    {
      refetchInterval: 10000,
    }
  );
};

export default useLightRailInfo;
